
.bg-primary {
  background: #3da8f5;
}
.color-danger {
  color: #E53935;
}
.color-gary {
  color: #636363;
}
.menu-navbar {
  position: relative;
  z-index: 100;
  height: 58px;
  background-color: rgba(255, 255, 255, 0.95);
  -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.15);
          box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.15);
  padding: 4px 10px;
}
.menu-navbar .user-head-img {
    border-radius: 50%;
    display: inline-block;
    height: 40px;
    margin-top: 5px;
    margin-left: 10px;
    width: 40px;
}
.menu-navbar .nav-item .split-border {
    vertical-align: middle;
    line-height: 30px;
    height: 30px;
    display: block;
    margin-top: 10px;
}
.menu-navbar .nav-item .nav-link {
    line-height: 50px;
    margin: 0;
    padding: 0 10px;
    font-size: 15px;
}
.menu-navbar .nav-item .nav-link .ion {
      font-size: 24px;
      line-height: 50px;
}
.ion-md {
  font-weight: normal;
  font-size: 28px;
  width: 30px;
  text-align: center;
  vertical-align: middle;
  line-height: 30px;
  height: 30px;
  padding: 0;
  cursor: pointer;
}
.ion-lg {
  font-size: 25px;
}



